
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}


.header
{
    height: 400px;
    background-image: -webkit-gradient(linear,left top, left bottom,from(rgba(0, 0, 0, 0.5)),to(rgba(5, 189, 221, 0.5))),url(/static/img/background.61de5c1.jpg);
    background-image: linear-gradient(rgba(0, 0, 0, 0.5),rgba(5, 189, 221, 0.5)),url(/static/img/background.61de5c1.jpg);
    background-size: cover;
    background-position:center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    font-family: sans-serif;
}
.header h1{
    color: #fff;
    padding:10px;
    border: none;
    border-radius: 25px;
    outline: none;
}
.column {
    float: left;
    width: 25%;
    padding: 0 10px;
}
/* Remove extra left and right margins, due to padding */
.row {margin: 0 -5px;
}
/* Clear floats after the columns */
.row:after {
    content: "";
    display: table;
    clear: both;
}
/* Responsive columns */
@media screen and (max-width: 600px) {
.column {
      width: 100%;
      display: block;
      margin-bottom: 20px;
}
}
/* Style the counter cards */
.card {
    -webkit-box-shadow: 0 4px 8px 0 rgba(11, 78, 75, 0.2);
            box-shadow: 0 4px 8px 0 rgba(11, 78, 75, 0.2);
    -webkit-box-align: end;
        -ms-flex-align: end;
            align-items: flex-end;
    padding: 16px;
    -ms-flex-line-pack: center;
        align-content: center;
    text-align: center;
    background-color:#f1f1f1;
    background-image: -webkit-gradient(linear,left top, left bottom,from(rgba(0, 195, 255, 0.5)),to(rgb(255, 255, 255)));
    background-image: linear-gradient(rgba(0, 195, 255, 0.5),rgb(255, 255, 255));
}
.movieList{
     width: 20rem; text-align:center;display:inline-block;  
     margin: 20px;
}
.card img {
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
  width: 300px;
  height: 300px;
}








































